import React from 'react';
import styled from 'styled-components';

import MenuPrinter from '@components/menu-printer';
import Menu from '@utils/menu';
import MenuForm from '@components/menu-form';


const Body = styled.main({
  display: 'flex',
  flexDirection: 'row',
  alignItems: 'center',
  justifyContent: 'stretch',
});

const App: React.FC = React.memo(function App () {
  const [menu, setMenu] = React.useState<Menu>({
    headerPic: '',
    groups: []
  });

  let [actions, actionFlag]: [{
    forward: (d: Readonly<Menu>) => Partial<Menu>;
    backward: (d: Readonly<Menu>) => Partial<Menu>;
  }[], number] = React.useMemo(() => [[], 0], []);

  const actionForward = React.useCallback(() => {
    if (actionFlag >= actions.length) {
      actionFlag = actions.length;

      return;
    }

    // 
    const next = {
      ...menu,
      ...actions[actionFlag]?.forward(menu),
    };
    actionFlag += 1;
    setMenu(next);
  }, [menu, setMenu, actions, actionFlag]);

  const actionBackward = React.useCallback(() => {
    if (actionFlag <= 0) {
      actionFlag = 0;

      return;
    }

    // 
    actionFlag -= 1;
    const next = {
      ...menu,
      ...actions[actionFlag]?.backward(menu),
    };
    setMenu(next);
  }, [menu, setMenu, actions, actionFlag]);

  const pushAction = React.useCallback(
    (forward: (d: Readonly<Menu>) => Partial<Menu>, backward: (d: Readonly<Menu>) => Partial<Menu>) => {
      // 
      actions = actions.slice(0, actionFlag);
      // 
      actions.push({ forward, backward });
      //  20 
      actions = actions.slice(0, 20);
      // 
      actionForward();
    },
    [actions, actionForward]
  );

  return (
    <Body>
      {/* form */}
      <MenuForm
        data={menu}
        pushAction={pushAction}
      />
      <MenuPrinter
        data={menu}
      />
    </Body>
  );
});

export default App;
